body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.line-chart {
  padding: 1px;
}

.line-chart .line {
  fill: none;
  stroke: steelblue;
  stroke-width: 1.5px;
}
.line-chart .zoom {
  cursor: move;
  fill: none;
  pointer-events: all;
}

.stream-table {
  text-align: left;
}

.stream-table .ReactTable {
  border: none;
}

.stream-table .ReactTable .pagination-top {
  width: 50%;
}

@media only screen and (min-width: 1025px) and (max-width: 1280px) {
  .stream-table .ReactTable .pagination-top {
    width: 60%;
  }
}

@media only screen and (min-width: 850px) and (max-width: 1024px) {
  .stream-table .ReactTable .pagination-top {
    width: 80%;
  }
}

@media only screen and (max-width: 849px) {
  .stream-table .ReactTable .pagination-top {
    width: 100%;
  }
}

.stream-table .ReactTable .rt-thead {
  background: #000;
  color: #fff;
}

.stream-table .ReactTable .rt-thead .rt-resizable-header-content {
  padding: 5px 0;
  font-size: 16px;
}

.stream-table .ReactTable .rt-thead .rt-th {
  border: 1px solid #ddd !important;
}

.stream-table .ReactTable .rt-tbody .rt-td {
  border: 1px solid #ddd !important;
}

.stream-table .ReactTable .rt-tbody .rt-tr-group {
  border-bottom: 1px solid #ddd !important;
}

.stream-table .ReactTable .rt-thead.-header {
  box-shadow: 0 2px 15px 0 #ddd;
}

.stream-table .ReactTable .long-url {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.stream-table .ReactTable .rt-thead .rt-th.-sort-desc {
  box-shadow: none !important;
}

.stream-table .ReactTable .rt-thead .rt-th.-sort-asc {
  box-shadow: none !important;
}

